"use client";

import { create } from "zustand";
import { useQuery, QueryClient } from "@tanstack/react-query";
import { ProductData, } from "@/schema/product"; // Adjust the import based on your project structure

interface ProductState {
  selectedProductId: string | null;
  isCreatingProduct: boolean;
  filters: {
    showInactive: boolean;
    searchTerm: string;
    sortBy: 'name' | 'price' | 'stockCount' | 'createdAt';
    sortOrder: 'asc' | 'desc';
  };
  
  // Actions
  selectProduct: (id: string | null) => void;
  setCreatingProduct: (creating: boolean) => void;
  toggleShowInactive: () => void;
  setSearchTerm: (term: string) => void;
  setSorting: (field: ProductState['filters']['sortBy'], order: ProductState['filters']['sortOrder']) => void;
  
  // API interactions
  fetchProducts: () => Promise<ProductData[]>;
  fetchProductById: (id: string) => Promise<ProductData>;
  createProduct: (data: ProductData) => Promise<ProductData>;
  updateProduct: (id: string, data: ProductData) => Promise<ProductData>;
  deleteProduct: (id: string) => Promise<void>;
  invalidateProducts: () => Promise<void>;
}

// Create the store
export const useProductStore = create<ProductState>((set, get) => ({
  selectedProductId: null,
  isCreatingProduct: false,
  filters: {
    showInactive: false,
    searchTerm: '',
    sortBy: 'createdAt',
    sortOrder: 'desc',
  },
  
  // Actions
  selectProduct: (id) => set({ selectedProductId: id }),
  setCreatingProduct: (creating) => set({ isCreatingProduct: creating }),
  toggleShowInactive: () => set((state) => ({ 
    filters: { 
      ...state.filters, 
      showInactive: !state.filters.showInactive 
    } 
  })),
  setSearchTerm: (term) => set((state) => ({
    filters: {
      ...state.filters,
      searchTerm: term
    }
  })),
  setSorting: (field, order) => set((state) => ({
    filters: {
      ...state.filters,
      sortBy: field,
      sortOrder: order
    }
  })),
  
  // API interactions
  fetchProducts: async () => {
    const res = await fetch("/api/dashboard/products", {
      headers: { Accept: "application/json" },
    });
    if (!res.ok) throw new Error("Failed to fetch products");
    return res.json();
  },
  
  fetchProductById: async (id) => {
    const res = await fetch(`/api/dashboard/products/${id}`, {
      headers: { Accept: "application/json" },
    });
    if (!res.ok) throw new Error("Failed to fetch product");
    return res.json();
  },
  
  createProduct: async (data) => {
    const res = await fetch("/api/dashboard/products", {
      method: "POST",
      headers: { 
        "Content-Type": "application/json",
        Accept: "application/json" 
      },
      body: JSON.stringify(data)
    });
    if (!res.ok) {
      const error = await res.json();
      throw new Error(error.message || "Failed to create product");
    }
    await get().invalidateProducts();
    return res.json();
  },
  
  updateProduct: async (id, data) => {
    const res = await fetch(`/api/dashboard/products/${id}`, {
      method: "PATCH",
      headers: { 
        "Content-Type": "application/json",
        Accept: "application/json" 
      },
      body: JSON.stringify(data)
    });
    if (!res.ok) {
      const error = await res.json();
      throw new Error(error.message || "Failed to update product");
    }
    await get().invalidateProducts();
    return res.json();
  },
  
  deleteProduct: async (id) => {
    const res = await fetch(`/api/dashboard/products/${id}`, {
      method: "DELETE"
    });
    if (!res.ok) {
      const error = await res.json();
      throw new Error(error.message || "Failed to delete product");
    }
    await get().invalidateProducts();
  },
  
  invalidateProducts: async () => {
    const queryClient = new QueryClient();
    await queryClient.invalidateQueries({ queryKey: ["products"] });
  }
}));

// Custom hook for accessing filtered products
export function useFilteredProducts() {
  const { showInactive, searchTerm, sortBy, sortOrder } = useProductStore((state) => state.filters);
  const fetchProducts = useProductStore((state) => state.fetchProducts);
  
  return useQuery<ProductData[]>({
    queryKey: ["products", { showInactive, searchTerm, sortBy, sortOrder }],
    queryFn: fetchProducts,
    select: (data) => {
      let filtered = data;
      
      // Filter by active status if needed
      if (!showInactive) {
        filtered = filtered.filter(product => product.isActive);
      }
      
      // Filter by search term if provided
      if (searchTerm) {
        const term = searchTerm.toLowerCase();
        filtered = filtered.filter(product => 
          product.name.toLowerCase().includes(term)
        );
      }
      
      // Sort the data
      filtered = [...filtered].sort((a, b) => {
        const valueA = a[sortBy];
        const valueB = b[sortBy];
        
        // Handle string comparison
        if (typeof valueA === 'string' && typeof valueB === 'string') {
          return sortOrder === 'asc' 
            ? valueA.localeCompare(valueB)
            : valueB.localeCompare(valueA);
        }
        
        // Handle number comparison
        return sortOrder === 'asc' 
          ? (valueA as number) - (valueB as number)
          : (valueB as number) - (valueA as number);
      });
      
      return filtered;
    }
  });
}

// Custom hook for accessing a single product by ID
export function useProduct(id: string | null) {
  const fetchProductById = useProductStore((state) => state.fetchProductById);
  
  return useQuery<ProductData>({
    queryKey: ["product", id],
    queryFn: () => fetchProductById(id as string),
    enabled: !!id // Only run the query if an ID is provided
  });
}