"use client";

import { useState } from "react";
import Link from "next/link";
import { Plus, Pencil, Package, Search, ArrowUpDown } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Input } from "@/components/ui/input";
import { Switch } from "@/components/ui/switch";
import { DeleteProductButton } from "./delete-product-button";
import { useProductStore, useFilteredProducts } from "@/store/productStore";
import { ProductData } from "@/schema/product";

export default function ProductsPage() {
  // Get state and actions from Zustand
  const { 
    toggleShowInactive, 
    setCreatingProduct, 
    isCreatingProduct,
    setSearchTerm,
    setSorting,
    filters: { showInactive, sortBy, sortOrder }
  } = useProductStore();
  
  // Get filtered products using our custom hook
  const { data: products = [], isLoading, isError } = useFilteredProducts();
  
  // Local state for search input (debounced before updating store)
  const [searchInput, setSearchInput] = useState("");
  
  // Handle search with debounce
  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchInput(value);
    
    // Debounce search term updates
    const timeoutId = setTimeout(() => {
      setSearchTerm(value);
    }, 300);
    
    return () => clearTimeout(timeoutId);
  };
  
  // Handle column sorting
  const handleSort = (column: typeof sortBy) => {
    if (sortBy === column) {
      // Toggle order if already sorting by this column
      setSorting(column, sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      // Default to ascending for new column
      setSorting(column, 'asc');
    }
  };
  
  // Helper to render sort indicator
  const getSortIndicator = (column: typeof sortBy) => {
    if (sortBy !== column) return null;
    
    return (
      <span className="ml-1">
        {sortOrder === 'asc' ? '↑' : '↓'}
      </span>
    );
  };

  if (isLoading) return <div className="p-6 text-center">Loading...</div>;
  if (isError) return <div className="p-6 text-center text-red-500">Failed to load products.</div>;

  if (!products.length && !searchInput) {
    return (
      <div className="p-6 flex flex-col items-center justify-center h-64 bg-gray-50 rounded-lg border border-dashed">
        <Package className="h-12 w-12 text-gray-400 mb-4" />
        <h3 className="text-lg font-medium mb-2">No products found</h3>
        <p className="text-gray-500 mb-4 text-center max-w-md">
          Get started by creating your first product to display in your store.
        </p>
        <Button onClick={() => setCreatingProduct(true)}>
          <Plus className="mr-2 h-4 w-4" />
          Add New Product
        </Button>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Products</h1>
        <div className="flex gap-4 items-center">
          <div className="flex items-center gap-2">
            <Switch 
              checked={showInactive} 
              onCheckedChange={toggleShowInactive}
            />
            <span className="text-sm">Show inactive</span>
          </div>
          
          <Button onClick={() => setCreatingProduct(true)}>
            <Plus className="mr-2 h-4 w-4" />
            Add Product
          </Button>
        </div>
      </div>
      
      <div className="mb-4">
        <div className="relative">
          <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
          <Input
            type="search"
            placeholder="Search products..."
            className="pl-8"
            value={searchInput}
            onChange={handleSearchChange}
          />
        </div>
      </div>

      {products.length > 0 ? (
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="cursor-pointer" onClick={() => handleSort('name')}>
                Name {getSortIndicator('name')}
              </TableHead>
              <TableHead className="cursor-pointer" onClick={() => handleSort('price')}>
                Price {getSortIndicator('price')}
              </TableHead>
              <TableHead className="cursor-pointer" onClick={() => handleSort('stockCount')}>
                Stock {getSortIndicator('stockCount')}
              </TableHead>
              <TableHead>Status</TableHead>
              <TableHead className="cursor-pointer" onClick={() => handleSort('createdAt')}>
                Created {getSortIndicator('createdAt')}
              </TableHead>
              <TableHead className="text-right">Actions</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {products.map((product) => (
              <TableRow key={product._id}>
                <TableCell className="font-medium">{product.name}</TableCell>
                <TableCell>${product.price.toFixed(2)}</TableCell>
                <TableCell>{product.stockCount}</TableCell>
                <TableCell>
                  <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                    product.isActive ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'
                  }`}>
                    {product.isActive ? 'Active' : 'Inactive'}
                  </span>
                </TableCell>
                <TableCell>{product.createdAt ? new Date(product.createdAt).toLocaleDateString() : 'N/A'}</TableCell>
                <TableCell className="text-right">
                  <div className="flex justify-end gap-2">
                    <Link href={`/dashboard/products/${product._id}`}>
                      <Button size="sm" variant="outline">
                        <Pencil className="h-4 w-4" />
                        <span className="sr-only">Edit</span>
                      </Button>
                    </Link>
                    <DeleteProductButton 
                      productId={product._id} 
                      
                      
                    />  
                  </div>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      ) : (
        <div className="text-center p-8 bg-gray-50 rounded-lg">
          <p>No products match your search criteria.</p>
        </div>
      )}
      
      {/* Product Form Dialog
      {isCreatingProduct && (
        <ProductFormDialog
          open={isCreatingProduct}
          onClose={() => setCreatingProduct(false)}
        />
      )} */}
    </div>
  );
}